//import React from "react"
import { useStaticQuery, graphql } from "gatsby"

const useDictionary = () => { //allDataCsv
  const data = useStaticQuery(graphql`
  query MyDictionaryQuery {
    allDictionaryCsv {
      totalCount
      edges {
        node {
        title
        dictionaryId
        comment
        type
        }
       }
     }
   }    
  `)
  return data.allDictionaryCsv
}

export default useDictionary;